<template>
  <section class="main">
    <h1 class="heading">
      <template v-if="query">
        {{ $t("SHOWING_RESULTS_FOR", { query }) }}
      </template>
      <template v-else>
        {{ $t("SEARCH") }}
      </template>
    </h1>
    <div class="wrapper">
      <aside class="filters">
        <filters :type="type" :genre="genre" :price="price"></filters>
      </aside>
      <div class="apps" ref="apps">
        <p class="not-found" v-if="!isLoading && !results.length">
          {{
            $t("NO_RESULTS_IN_SECTION", {
              section:
                (genre &&
                  $t($store.getters.genres[type][genre].name.toUpperCase())) ||
                translateType(type),
            })
          }}
        </p>
        <app
          ref="app"
          v-for="app in results"
          :app="app"
          :type="type"
          :key="app.id"
        />
        <loader class="apps__loader" v-show="!isLastPage"></loader>
      </div>
      <aside class="categories">
        <h2 class="categories__heading">{{ $t("EXPLORE_CATEGORIES") }}</h2>
        <ul>
          <li class="category" v-for="genre in genres" :key="genre.id">
            <a
              class="category__link"
              href="#"
              @click.prevent="goToGenre(genre.id)"
            >
              {{ $t(genre.name.toUpperCase()) }}
              <span class="category__count">{{ genre.content_amount }}</span>
            </a>
          </li>
        </ul>
      </aside>
    </div>
  </section>
</template>

<script>
import Filters from '@/components/Filters';
import App from '@/components/App';
import Loader from '@/components/Loader';
import { throttle, isEqual } from 'lodash';

export default {
  name: 'search',
  components: { Filters, App, Loader },
  data() {
    return {
      isLoading: true,
      results: [],
      page: 1,
      isLastPage: false,
      params: { ...this.$route.query },
    };
  },
  computed: {
    title() {
      return this.$t('SEARCH');
    },
    query() {
      return this.$route.query.query;
    },
    type() {
      const { type } = this.$route.query;
      return this.translateType(type) ? type : 'cydia';
    },
    dev() {
      return this.$route.query.dev;
    },
    genre() {
      const { genre } = this.$route.query;
      if (genre && !this.$store.getters.genres[this.type][genre]) {
        return null;
      }
      return genre;
    },
    price() {
      const { price } = this.$route.query;
      if (price !== '1' && price !== '2') {
        return null;
      }

      return price;
    },
    genres() {
      return Object.values(this.$store.getters.genres[this.type] || {})
        .sort((a, b) => b.content_amount - a.content_amount);
    },
  },
  methods: {
    translateType(type) {
      const dict = {
        cydia: `${this.$t('{{brand}}')} Store ${this.$t('APPS')}`,
        ios: `Apple AppStore ${this.$t('APPS')}`,
        tvos: 'tvOS',
      };

      return dict[type];
    },
    async resetScroll() {
      await this.$nextTick();
      const rect = (this.$refs.apps && this.$refs.apps.getBoundingClientRect()) || { top: -1 };
      if (rect.top < 0) {
        window.scrollTo(0, 0);
      }
    },
    goToGenre(id) {
      this.$router.push({ query: { ...this.$route.query, genre: id } });
    },
    search(page) {
      if (!page) {
        this.results = [];
        this.page = 1;
      }
      this.isLoading = true;
      const params = { type: this.type };
      if (this.query) {
        params.q = this.query;
      }
      if (this.dev) {
        params.dev = this.dev;
      }
      if (this.genre) {
        params.genre = this.genre;
      }
      if (this.price) {
        params.price = this.price;
      }
      if (page) {
        params.page = page;
      }
      return this.$api.search(params).then((data) => {
        this.isLoading = false;
        if (data.length < 25) {
          this.isLastPage = true;
        }
        this.results = [...this.results, ...data];
      });
    },
    // eslint-disable-next-line
    handleScroll: throttle(function () {
      if (this.isLoading || this.isLastPage || !this.$refs.app || this.$route.name !== 'search') {
        return;
      }

      const index = this.results.length - 20;
      const app = this.$refs.app[index >= 0 ? index : 0];
      if (!app) {
        return;
      }
      const breakpoint = app.$el.offsetTop;

      if (window.scrollY > breakpoint) {
        this.page = this.page + 1;
        this.search(this.page);
      }
    }, 1000),
  },
  watch: {
    '$route.query': {
      handler() {
        if (this.$route.name === 'search' && !isEqual(this.$route.query, this.params)) {
          this.params = { ...this.$route.query };
          this.isLastPage = false;
          this.resetScroll();
          this.search();
        }
      },
      deep: true,
    },
  },
  serverPrefetch() {
    return this.search();
  },
  created() {
    this.search();
  },
  mounted() {
    this.resetScroll();
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
};
</script>

