<template>
  <div class="filters">
    <h2 class="filters__heading">
      <img class="filters__icon"
        :src="require(`@/assets/themes/${BRAND_KEY}/images/icon-filter.svg`)" />
      {{ $t("FILTER_BY") }}
    </h2>
    <v-select
      class="select"
      :options="[
        { label: `Apple AppStore ${this.$t('APPS')}` , value: 'ios' },
        { label: `${this.$t('{{brand}}')} Store ${this.$t('APPS')}`, value: 'cydia' },
        { label: `Apple TV ${this.$t('APPS')}`, value: 'tvos' },
      ]"
      :reduce="(item) => item.value"
      :value="type"
      :clearable="false"
      :searchable="false"
      @input="updateParams({ type: $event, genre: null })"
    ></v-select>
    <v-select
      class="select"
      :value="genre"
      :options="genres"
      :searchable="false"
      :reduce="(item) => item.value"
      :placeholder="$t('CATEGORY')"
      @input="updateParams({ genre: $event })"
    ></v-select>
    <v-select
      class="select"
      :value="price"
      :options="[
        { label: $t('PAID'), value: '1' },
        { label: $t('FREE'), value: '2' },
      ]"
      :reduce="(item) => item.value"
      :searchable="false"
      :placeholder="$t('PRICE')"
      @input="updateParams({ price: $event })"
    ></v-select>
  </div>
</template>

<script>
export default {
  props: ['type', 'genre', 'price'],
  computed: {
    genres() {
      if (!this.$store.getters.genres[this.type]) {
        return [];
      }

      return Object.entries(this.$store.getters.genres[this.type])
        .map(genre => ({ label: this.$t(genre[1].name.toUpperCase()), value: genre[0] }));
    },
  },
  methods: {
    updateParams(obj) {
      const params = { ...this.$route.query, ...obj };
      Object.keys(params).forEach((key) => {
        if (!params[key]) {
          delete params[key];
        }
      });
      this.$router.push({ query: params });
    },
  },
};
</script>

